.about-us {
    &__content {
        max-width: 35rem;
        margin-right: 6rem;
    }
    &__title {
        margin: 8rem 0 2rem;
    }
    &__text {
        margin-bottom: 4rem;
    }

    &__img-box {
        display: flex;
        justify-content: space-between;
        column-gap: 1rem;
        margin-right: -2rem;
    }

    &__box1 {
        display: flex;
        flex-direction: column;
        row-gap: 1rem;
        max-width: 20rem;
    }
    &__box2 {
        margin-top: 4rem;
    }
}
//-------screens------------------

@media (max-width: 1300px) {
    .about-us {
        &__inner {
            justify-content: center;
            text-align: center;
        }
        &__content {
            max-width: 90%;
            margin-right: 0;
        }
        &__title {
            margin: 0 0 2rem;
        }
        &__img-box {
            margin: 0 1rem;
        }
    }
}
